<template>
	<div class="container" v-loading.body="loading" element-loading-text="拼命加载中">
		<p class="city-select-txt">城市筛选</p>
		<el-tabs v-model="activeCity" type="card" @tab-click="handleClick">
	    <el-tab-pane label="全部" name="全部" code="0"></el-tab-pane>
	    <el-tab-pane v-for="v in cityData" :label="v.city_name" :name="v.city_name" :code="v.city_code" ></el-tab-pane>
  	</el-tabs>

		<el-table

		 :data="listData"
		 border
		 @sort-change="sortChange"
		 style="width: 100%">

		 <el-table-column
			 prop="admin_id"
			 label="专员ID"
			 width="100">
		 </el-table-column>
		 <el-table-column
			 label="专员姓名"
			 width="100">
			 <template scope="scope">
					 <div :title="scope.row.name" @click="checkData(scope.row.admin_id, scope.row.name)" style="cursor:pointer;width:70px;text-overflow:ellipsis;color:#20a0ff; white-space:nowrap;overflow:hidden;" >{{ scope.row.name }}</div>
      </template>
		 </el-table-column>
		 <el-table-column
			 prop="city"
			 label="所在城市"
			 width="110">
		 </el-table-column>
		 <el-table-column
			 prop="all_customer_count"
			 label="成交客户数"
			 width="130"
			 label-class-name="table-title"
			 sortable='custom'
			 >
		 </el-table-column>
		 <el-table-column
			 prop="create_by_own_customer_count"
			 label="自己创建客户数"
			 width="160"
			 label-class-name="table-title"
			 sortable='custom'>
		 </el-table-column>
		 <el-table-column
			 prop="can_see_customer_count"
			 label="可查看客户数"
			 width="150"
			 label-class-name="table-title"
			 sortable='custom'>
		 </el-table-column>
		 <el-table-column
			 prop="wait_for_create_customer"
			 label="待创建客户数"
			 width="150"
			 label-class-name="table-title"
			 sortable='custom'>
		 </el-table-column>
		 <el-table-column
			 prop="create_by_own_customer_proportion"
			 label="创建客户比例"
			 width="150"
			 label-class-name="table-title"
			 sortable='custom'>
		 </el-table-column>
		 <el-table-column
			 prop="all_perfect_fields"
			 label="总完善字段数"
			 width="150"
			 label-class-name="table-title"
			 sortable='custom'>
		 </el-table-column>
		 <el-table-column
			 prop="average_each_customer_field_count"
			 label="每客户平均字段数"
			 width="180"
			 label-class-name="table-title"
			 sortable='custom'>
		 </el-table-column>
		 <el-table-column
			 prop="median_customer_field"
			 label="每客户字段中位数"
			 width="180"
			 label-class-name="table-title"
			 sortable='custom'>
		 </el-table-column>
		 <el-table-column
			 prop="max_customer_field_count"
			 label="单个客户最高字段数"
			 width="190"
			 label-class-name="table-title"
			 sortable='custom'>
		 </el-table-column>
		 <el-table-column
			 prop="min_customer_field_count"
			 label="单个客户最少字段数"
			 width="190"
			 label-class-name="table-title"
			 sortable='custom'>
		 </el-table-column>

	 </el-table>
	 <!-- pagination -->
	 <el-pagination
		 @current-change="handleCurrentChange"
		 :page-size="10"
		 layout="total, prev, pager, next"
		 :total="pageParam.total"
		 style="margin-top:20px;"
		 >
	 </el-pagination>
	 <div id="J_dataWrap" class="data-layer" v-show="isShowData">
		 <div class="data-content">

			 	<div class="data-content-wrap" v-loading="loading2" element-loading-text="拼命加载中">
					<button @click="hideChartLayer" type="button" aria-label="Close" class="el-message-box__headerbtn" style="z-index:20;"><i class="el-message-box__close el-icon-close"></i></button>
					<div class="agent-info">
						<span>专员id&nbsp;&nbsp;<i>{{currentAgent.admin_id}}  </i></span>
						<span>专员姓名&nbsp;&nbsp;<i>{{currentAgent.name}}</i></span>
						<span>可查看客户总数&nbsp;&nbsp;<i>{{currentAgent.can_see_customer_count}}</i></span>
						<span>客户信息平均完善比例&nbsp;&nbsp;<i>{{currentAgent.average_perfection_degree}}</i></span>
					</div>
					<div class="legend-wrap">
						<span></span>&nbsp;客户信息完善比例&nbsp;&nbsp;
						<span></span>&nbsp;此专员贡献字段比例
					</div>
					<div class="el-icon-arrow-left change-chart change-chart-left" @click="chartLeft" v-show="leftChangeChart"></div>
					<div class="el-icon-arrow-right change-chart change-chart-right" @click="chartRight" v-show="rightChangeChart"></div>

					<div id="J_data" class="data-inner-content" :style="[chartWidth]">

					</div>
				</div>
		</div>
	 </div>

	</div>
</template>

<script>

export default {

	data: function () {
		return {
				loading: true,
				loading2: true,
				activeCity: '全部',
				isShowData: false,
				listParam:{
					"page": "1"
				},
				pageParam: {},
				chartParam:{
					page:1
				},
				currentAgent:{
					can_see_customer_count:0,
				},
				cityData:[],
				listData: [],
				chartWidth:{
					width: '1100px'
				},
				myChart:null,
				chartPage: 0,
				leftChangeChart:false,
				rightChangeChart:true

		}
	},
	mounted(){
		var self = this;
		self.loading = true;
		self.getList();
		self.getCity();
	},
	methods: {
		chartLeft() {
			var self = this;
			self.myChart.destroy();
			self.loading2 = true;
			self.chartWidth.width = '1100px';
			let curPage = --self.chartParam['page'];
			if (curPage == 1) {
				self.leftChangeChart = false;
			} else {
				self.rightChangeChart = true;
			}

			self.getChartData();
		},
		chartRight() {
			var self = this;
			self.myChart.destroy();
			self.loading2 = true;
			self.chartWidth.width = '1100px';
			let curPage = ++self.chartParam['page'];

			if (curPage >= self.chartPage) {
				self.rightChangeChart = false;
			} else {
				self.leftChangeChart = true;
			}
			self.getChartData();
		},
		handleClick(tab, event) {
      var tabName = tab.name;
      var self = this;
			var code = tab.$attrs.code;
			self.listParam.city = code;
			self.loading = true;
			self.getList();
    },
		handleCurrentChange(val) {
			var self = this;
			self.listParam['page'] = val;
			self.loading = true;
			this.getList();
		},
		checkData(admin_id, agentName) {
			// 基于准备好的dom，初始化echarts实例
			var self = this;
			self.isShowData = true;
			self.chartParam['admin_id'] =  admin_id;
			self.currentAgent['admin_id'] = admin_id;
			self.currentAgent['name'] = agentName;
			self.rightChangeChart = true;
			self.leftChangeChart = false;
			self.getChartData();


		},
		getChartData(){
			var self = this;
			self.$ajax.get(self.$api.getModuleHost('CRM', "/manage/getAdminCustomerPerfectProportion"),self.chartParam).then(function (data) {
				var chartData = data.data;
				self.chartPage = data.last_page
				if (self.chartPage <= 1) {
					self.rightChangeChart = false;
					self.leftChangeChart = false;
				}
				var name = chartData.name;
				if (name.length == 0) {
					self.loading2 = false;
					var chartWrap = document.getElementById('J_data');
					chartWrap.innerHTML = '暂无数据'
					return;
				}

				var perfection_degree = chartData.perfection_degree;
				var admin_perfection_degree = chartData.admin_perfection_degree;


				if (name.length <= 50) {
					self.chartWidth = {
						width: '1100px'
					}
				} else {
					self.chartWidth = {
						width: (40*name.length) + 'px'
					}
				}

				self.currentAgent['can_see_customer_count'] = data.can_see_customer_count;
				self.currentAgent['average_perfection_degree'] = data.average_perfection_degree;


				var options = {
			        chart: {
			            type: 'column'
			        },
							legend:{
								enabled: false
							},
							title: {
			            text: ''
			        },
			        xAxis: {
			            categories: name,
			        },
			        yAxis: {
			            allowDecimals: true,
			            min: 0,
									title: {
											 text: ''
									},
									labels:{
										align:'center'
									}
			        },
			        tooltip: {
			            formatter: function () {
			                return '<b>' + this.x + '</b><br/>' +
			                    this.series.name + ': ' + this.y + '%<br/>'
			            }
			        },
			        plotOptions: {
			            column: {
			                stacking: 'normal',
											pointWidth:5,//设置柱状图宽度
											dataLabels:{
								        enabled:true,       //是否显示数据标签
								        color:'#e3e3e3',        //数据标签字体颜色
								        formatter: function() {    //数据标签数据格式化
								                    return (this.y) + '%';
								         }
											}
									}
			        },
			        series: [{
			            name: '客户信息完善比例',
			            data: perfection_degree,
									color: '#F7A35C',
			            stack: 'male'
			        },{
			            name: '此专员贡献字段比例',
			            data: admin_perfection_degree,
									color: '#90ED7D',
			            stack: 'male'
			        }]
			    }
				setTimeout(function(){
					self.myChart = self.$HighCharts.chart('J_data',options)
					self.loading2 = false;
				},100)





			})
		},
		getList() {
			var self = this;
			self.$ajax.get(self.$api.getModuleHost('CRM', "/manage/getadminlist"),self.listParam).then(function (data) {
				self.listData = data.data;
				self.pageParam['total'] = data.total;
				self.loading = false;
			})
		},
		getCity() {
			var self = this;
			self.$ajax.get(self.$api.getModuleHost('CRM', "/manage/getcitys")).then(function (data) {
				self.cityData = data.data

			})
		},
		sortChange(column) {
			var self = this;
			var order = column.order == 'ascending'? order = 0: order = 1;
			self.listParam.sort = column.prop;
			self.listParam.order = order;
			self.loading = true;
			self.getList();
		},
		hideChartLayer() {
			var self = this;
			self.loading2 = true;
			self.isShowData = false;
			self.myChart.destroy();
			self.rightChangeChart = true;
			self.leftChangeChart = false;
			self.chartParam.page = 1;

		},
	},
	components: {

	}
}
</script>

<style lang="less" >
	.container{
		min-width:1366px;
		overflow-x:auto;
	}
	.city-select-txt{
		font-size:14px;
		margin:10px 0;
	}
	.table-title{
		cursor:pointer;
	}
	.data-layer{
		width:100%;
		height:100%;
		background:rgba(0, 0, 0, .5);
		position:fixed;
		top:0;
		left:0;
		z-index:10;
		text-align:center;

		.data-content{
			position:absolute;
			width:1100px;
			height:600px;
			background:#fff;
			text-align:left;
			top:50%;
			left:50%;
			margin-top:-300px;
			margin-left:-550px;
			border-radius:3px;
			.data-content-wrap{
				overflow-x:auto;
			}
			.change-chart{
				position:absolute;
				top:50%;
				width:50px;
				height:60px;
				background:rgba(0,0,0,.5);
				color:#fff;
				z-index:10;
				font-size:20px;
				text-align:center;
				line-height:60px;
				margin-top:-30px;
				cursor:pointer;
				&:hover{
					background:rgba(0,0,0,.4);
				}
			}
			.change-chart-left{
				left:0;
			}
			.change-chart-right{
				right:0;
			}
			.data-inner-content{
				height:500px;
				padding-top:100px;
				text-align:center;
				line-height:500px;
				font-size:14px;
			}
			.agent-info{
				font-size:14px;
				position:absolute;
				top:20px;
				left:20px;
				color:#333;
				span{
					display:inline-block;
					margin-right:40px;
					z-index:20;
					i{
						color:#20a0ff;
					}
				}
			}
			.legend-wrap{
				position:absolute;
				top:70px;
				left:390px;
				text-align:center;
				font-size:14px;
				span{
					display:inline-block;
					width:30px;
					height:15px;
					border-radius:3px;

					vertical-align:bottom;
					&:first-child{
						background:#F7A35C;
					}
					&:last-child{
						background:#90ED7D;
					}
				}
			}
		}
	}

</style>



